import { useTranslation } from "react-i18next";
import Modal from "../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import style from "../../FinanceManager/Income/Income.module.css";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import { useEffect, useState } from "react";
import { assestsUserFinanceUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import QvipleLoader from "../../../Loader/QvipleLoader";
import CreateButton from "../../../Ui/Button/CreateButton";
import FormInput from "../../../Ui/Input/FormInput";
import {
  existingRequiredField,
  requiredField,
} from "../../../Validation/functions/checkingRequiredField";
import PaymentGatewayConfig from "../../../../PaymentGateway/PaymentGatewayConfig";
import { get_initiate_paytmV2 } from "../../../../Decrypt/paytmUpiInitiative";
import { useParams } from "react-router-dom";
import TileWrapper from "../../../Ui/Tab/Tile/TileWrapper";
import TileCard from "../../../Ui/Tab/Tile/TileCard";
import { paymentCollectType } from "../../../Constant/StudentProfile/student_profile_constant";
import FormInputView from "../../../Ui/Input/FormInputView";
import {
  useStudentAdmissionFeeCardBankValidate,
  useStudentInstituteCharges,
} from "../../../Hooks/ApiHooks/StudentProfile/student-manager-api";
import NotifyEvent from "../../../Utils/Snackbar/NotifyEvent";
const StudentAdmissionPayFees = ({
  onClose,
  install,
  admission,
  isEditable,
  statusId,
}) => {
  const { t } = useTranslation();
  const params = useParams();
  const [disabled, setDisabled] = useState(false);
  const [state, setState] = useState({
    amount: 0,
    type: "Admission",
    isTransaction: false,
    pay_remain: false,
  });
  const [errorField, setErrorField] = useState("");
  const [totalAmount, setTotalAmount] = useState(0);
  const [snackbar, setSnackbar] = useState({
    open: false,
    msg: "",
    variant: "error",
  });

  const { studentInstituteCharges } = useStudentInstituteCharges({
    data: {
      id: install?.instituteId,
    },
    skip: !install?.instituteId,
  });
  const [studentAdmissionFeeCardBankValidate] =
    useStudentAdmissionFeeCardBankValidate();

  useEffect(() => {
    if (install?.remainAmount) {
      setState({
        amount: install?.remainAmount ?? 0,
        type: "Admission",
        isTransaction: false,
        pay_remain: false,
      });
    }
  }, []);
  const onInputAmount = (e) => {
    setState((pre) => ({
      ...pre,
      amount: +e.target.value,
    }));
  };

  const onCloseGateway = () => {
    setState((prev) => ({
      ...prev,
      isTransaction: false,
    }));
  };

  const onCollectType = (val) => {
    setState((prev) => ({
      ...prev,
      pay_remain: val,
    }));
    if (val) {
      let t = 0;
      for (let rm of admission?.applicable_card?.remaining_array)
        if (rm?.status !== "Paid") t += rm?.remainAmount;
      setTotalAmount(t);
      setState((prev) => ({
        ...prev,
        amount: t ?? 0,
      }));
    }
  };

  const onCollectFees = () => {
    let validation = requiredField({
      amount: state.amount,
    });
    if (existingRequiredField(validation)) {
      if (
        state.pay_remain &&
        state.amount < totalAmount &&
        +admission?.fee_structure?.total_installments > 1
      ) {
        setErrorField({
          rest_issue: "Amount should not be less than total remaining fees",
        });
      } else {
        if (
          !state.pay_remain &&
          state.amount < install?.remainAmount &&
          +admission?.fee_structure?.total_installments > 1
        ) {
          setErrorField({
            rest_issue: "Amount should not be greater than remaining fees",
          });
        } else {
          if (studentInstituteCharges?.institute) {
            if (
              install?.appId &&
              admission?.appId?.admissionAdmin?.institute?.financeDepart?.[0]
            ) {
              setDisabled((pre) => !pre);
              studentAdmissionFeeCardBankValidate({
                fid: admission?.appId?.admissionAdmin?.institute
                  ?.financeDepart?.[0],
                appId: install?.appId,
                flow: "BY_DEPARTMENT",
              })
                .then((res) => {
                  setDisabled((pre) => !pre);
                  if (res?.data?.denied) {
                    setSnackbar({
                      open: true,
                      msg: "Account is not activated",
                      variant: "error",
                    });
                  } else {
                    setState((prev) => ({
                      ...prev,
                      isTransaction: true,
                    }));
                  }
                })
                .catch({});
            }
          } else {
            setSnackbar({
              open: true,
              msg: "Online Payment Not Activated",
              variant: "error",
            });
          }
        }
      }
    } else {
      setErrorField(validation);
    }
  };
  const checkoutHandler = () => {
    let gatewayCharges = (+state?.amount * 2.1) / 100;
    let gst = (+gatewayCharges * 18) / 100;
    let withGst = gatewayCharges + gst;
    const data = parseInt(state?.amount);
    var paymentData = {
      amount: Math.ceil(data + +withGst.toFixed(2)),
    };
    // let obj = {
    //   amount: +state?.amount ?? 10,
    //   amount_nocharges: +paymentData.amount ?? 0,
    //   type: "Admission",
    //   paidBy: statusId ? `${admission?.student}` : `${admission?.student?._id}`,
    //   paidTo: `${install?.instituteId}`,
    //   moduleId: `${install?.appId}`,
    //   ad_status_id: statusId ? statusId : null,
    //   payment_installment: install?.installmentValue,
    //   ad_install: null,
    //   month: null,
    //   payment_remain_1: null,
    //   payment_card_type: null,
    //   payment_book_id: null,
    //   name: params.username,
    //   isApk: "web",
    //   payment_card_id: admission?._id ?? null,
    //   payment_remain_fees: state.pay_remain,
    //   charge: {
    //     num_platform_percent:
    //       studentInstituteCharges?.platform_charges?.percentage ?? 0,
    //     num_platform_max:
    //       studentInstituteCharges?.platform_charges?.maximum ?? 0,
    //     num_trans_pecent:
    //       studentInstituteCharges?.transaction_charges?.percentage ?? 0,
    //     num_trans_max:
    //       studentInstituteCharges?.transaction_charges?.maximum ?? 0,
    //     num_app_max: studentInstituteCharges?.application_charges ?? 0,
    //   },
    // };
    // console.info("dfnd", obj);
    get_initiate_paytmV2({
      amount: +state?.amount ?? 10,
      amount_nocharges: +paymentData.amount ?? 0,
      type: "Admission",
      paidBy: statusId ? `${admission?.student}` : `${admission?.student?._id}`,
      paidTo: `${install?.instituteId}`,
      moduleId: `${install?.appId}`,
      ad_status_id: statusId ? statusId : null,
      payment_installment: install?.installmentValue,
      ad_install: null,
      month: null,
      payment_remain_1: null,
      payment_card_type: null,
      payment_book_id: null,
      name: params.username,
      isApk: "web",
      payment_card_id: admission?._id ?? null,
      payment_remain_fees: state.pay_remain,
      charge: {
        num_platform_percent:
          studentInstituteCharges?.platform_charges?.percentage ?? 0,
        num_platform_max:
          studentInstituteCharges?.platform_charges?.maximum ?? 0,
        num_trans_pecent:
          studentInstituteCharges?.transaction_charges?.percentage ?? 0,
        num_trans_max:
          studentInstituteCharges?.transaction_charges?.maximum ?? 0,
        num_app_max: studentInstituteCharges?.application_charges ?? 0,
      },
      nsid: install?._id,
    })
      .then((data) => {
        // console.info("ldskjfnsbkjasdnf,ms", data);
        window.onScriptLoad(
          data?.response?.body?.txnToken,
          data?.order,
          data?.amount
        );
      })
      .catch((e) => {
        console.info(e);
      });
  };
  // console.info(
  //   "studentInstituteCharges",
  //   studentInstituteCharges,
  //   admission,
  //   install
  // );

  return (
    <>
      {state.isTransaction ? (
        <PaymentGatewayConfig
          onClose={onCloseGateway}
          allContent={{
            ...state,
            platform_charges:
              studentInstituteCharges?.platform_charges?.percentage,
          }}
          checkoutAdmissionHandler={checkoutHandler}
          isOptimzed
        />
      ) : (
        <Modal onClose={onClose}>
          <div className={style.modal_container}>
            <UserMemberContentWrapper>
              <div className={style.modal_container_outer_header}>
                <div className={style.modal_container_header}>
                  <h6>{t("pay_admission_fees")}</h6>
                </div>
                <img
                  src={`${assestsUserFinanceUrl}/close.svg`}
                  alt="close icon"
                  onClick={onClose}
                />
              </div>
            </UserMemberContentWrapper>
            <BorderBottom />
            <UserMemberContentWrapper
              customStyle={{
                paddingTop: "0",
              }}
            >
              <div className={style.admission_fees_row}>
                <h6>{t("applicable_pendig_fees_only")} :</h6>
                <h6>
                  {t("rs_only")}{" "}
                  {admission?.applicable_card?.applicable_fee ?? 0}
                </h6>
              </div>

              <div className={style.admission_fees_row}>
                <h6>
                  {t("fees_category")} :{" "}
                  {admission?.fee_structure?.category_master?.category_name ??
                    ""}
                </h6>
              </div>

              {admission?.fee_structure?.total_installments ? (
                +admission?.fee_structure?.total_installments > 1 &&
                isEditable ? (
                  <TileWrapper label="pay_rest_remaining_fees">
                    {paymentCollectType?.map((tile) => (
                      <TileCard
                        key={tile?.id}
                        tile={tile}
                        onTile={onCollectType}
                        activeTile={state.pay_remain}
                      />
                    ))}
                  </TileWrapper>
                ) : null
              ) : null}

              {isEditable ? (
                <FormInput
                  label={t(
                    !state.pay_remain
                      ? "enter_amount_to_be_paid"
                      : "amount_remain_installment"
                  )}
                  placeholder={t("please_enter_amount_paid")}
                  name={"amount"}
                  value={state.amount}
                  type={"tel"}
                  onChange={onInputAmount}
                  errorShow={errorField["amount"]}
                  msg={errorField["rest_issue"]}
                />
              ) : (
                <FormInputView
                  label={`${t(
                    !state.pay_remain
                      ? "amount_to_be_paid"
                      : "amount_remain_installment"
                  )}`}
                  value={state.pay_remain ? totalAmount : state.amount}
                />
              )}

              <CreateButton
                label="pay_admission_fees"
                onAction={onCollectFees}
              />
            </UserMemberContentWrapper>
          </div>
        </Modal>
      )}

      {disabled && <QvipleLoader />}
      <NotifyEvent eventState={snackbar} eventStateHandler={setSnackbar} />
    </>
  );
};

export default StudentAdmissionPayFees;
